*
{
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
header{
  background-color: black;
  align-items: center;
  font-size:30px ;
  font-weight: bold;
  padding:20px;
  color: white;
  text-align: center;
  letter-spacing:3px;
  justify-content: center;
  font-family: Arial, Helvetica, sans-serif;

}
.container{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80vh;
}
.quiz-start{
  flex-direction: column;
  gap:20px;
}
.flex-center{
  display:flex;
  align-items: center;
  justify-content: center;

}
.quiz-start button{
  background-color: black;
  color:white;
  padding: 5px 9px;
  border-radius: 3px;
}
.wrapper{
  border:1px solid gray;
  border-radius: 3px;
  padding:15px;
  min-width: 200px;
  display: flex;
  flex-direction: column;
  gap:30px;
}
.options{
 display: flex;
 flex-direction: column;
 gap:10px;
}
.question{
  font-weight: bold;

}
.option input{
  margin-right: 5px;
}
.wrapper button{
  background-color: black;
  color: white;
  padding: 7px;
  border-radius: 3px;
}
.questions{
  font-weight: bold;
}
.result{
  text-align: center;
}
.result button{
  background-color: black;
  color:white;
  padding: 3px 7px;
  border-radius: 3px;
  margin-top: 10px;
}
.result .text{
  line-height: 2;
}
